import React, { PureComponent } from "react";
import styled from "styled-components";
import Card from "../component/ProfileCard";

import ani from "animejs";
import SunImg from "../assets/img/speaker.sq.png";
import ZhuImg from "../assets/img/speaker.zhuxy.png";
import QiImg from "../assets/img/speaker.qixi.png";
import LiuImg from "../assets/img/speaker.lyx.png";

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  padding: 4rem 2rem 0 2rem;
  height: 100vh;
  .title {
    color: #ff6a1c;
    font-size: 1rem;
    font-weight: 800;
    box-shadow: inset 0 -0.4em #ccc;
    margin-bottom: 0.8rem;
  }
  .subTitle {
    color: #4d4d4d;
    font-size: 0.8rem;
    margin-bottom: 1rem;
  }
  .department {
    color: #fff;
    background: #ff6a14;
    border-radius: 0.2rem;
    font-weight: 800;
    font-size: 1.2rem;
    padding: 0.3rem 0.8rem;
  }
`;
export default class SpeakerTeam2 extends PureComponent {
  constructor() {
    super();
    this.wrapper = React.createRef();
  }
  componentDidMount() {
    const wrapper = this.wrapper.current;
    const eles = wrapper.querySelectorAll(".profile .avatar,.profile .intro");
    ani({
      targets: eles,
      delay: (el, i) => i * 90,
      opacity: [0, 1]
    });
  }

  render() {
    return (
      <Content ref={this.wrapper}>
        <h1 className="title"></h1>
        <h2 className="subTitle"></h2>
        <Card
          avatar={SunImg}
          name="  "
          descs={[
            "",
            ""
          ]}
        />

        <Card
          avatar={ZhuImg}
          name=""
          descs={[
            "",
            "MBA",
            ""
          ]}
        />

        <Card
          avatar={QiImg}
          name="  "
          descs={[
            "",
            "",
            "AMI"
          ]}
        />

        <Card
          avatar={LiuImg}
          name=""
          descs={[
            "",
            "",
            ""
          ]}
        />

        <p className="department"></p>
      </Content>
    );
  }
}
