import React, { PureComponent } from "react";
import styled from "styled-components";
import Card from "../component/ProfileCard";

import ani from "animejs";
import ChaoImg from "../assets/img/speaker.dongzc.png";
import LongImg from "../assets/img/speaker.zhangyanlong.png";
import KeImg from "../assets/img/speaker.huangke.png";

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  padding: 4rem 1rem 0 1rem;
  height: 100vh;
  .title {
    color: #ff6a1c;
    font-size: 1.2rem;
    font-weight: 800;
    box-shadow: inset 0 -0.4em #ccc;
    margin-bottom: 0.8rem;
  }
  .subTitle {
    color: #4d4d4d;
    font-size: 0.8rem;
    margin-bottom: 1.2rem;
  }
  .department {
    color: #fff;
    background: #ff6a14;
    border-radius: 0.2rem;
    font-weight: 800;
    font-size: 1.2rem;
    padding: 0.3rem 0.8rem;
  }
`;
export default class SpeakerTeam1 extends PureComponent {
  constructor() {
    super();
    this.wrapper = React.createRef();
  }
  componentDidMount() {
    const wrapper = this.wrapper.current;
    const eles = wrapper.querySelectorAll(".profile .avatar,.profile .intro");
    ani({
      targets: eles,
      delay: (el, i) => i * 90,
      opacity: [0, 1]
    });
  }

  render() {
    return (
      <Content ref={this.wrapper}>
        <h1 className="title"></h1>
        <h2 className="subTitle"></h2>
        <Card
          avatar={ChaoImg}
          name=""
          title=""
          descs={["", ""]}
        />

        <Card
          avatar={LongImg}
          name=""
          descs={[
            "",
            "",
            ""
          ]}
        />

        <Card
          avatar={KeImg}
          name=""
          descs={[
            "",
            ""
          ]}
        />

        <Card
          avatar={KeImg}
          name=""
          descs={[
            "",
            "",
            "/"
          ]}
        />

        <p className="department"></p>
      </Content>
    );
  }
}
